import { defineComponent, h, renderSlot } from 'vue';

const Slot = defineComponent({
  render() {
    return renderSlot(this.$slots, 'default', this.$props, () => {
      return [h('div', { class: 'lc-container' })];
    });
  },
});

Object.assign(Slot, {
  displayName: 'Slot',
  componentMetadata: {
    componentName: 'Slot',
    configure: {
      props: [
        {
          name: '___title',
          title: '',
          setter: 'StringSetter',
          defaultValue: '',
        },
        {
          name: '___params',
          title: '',
          setter: {
            componentName: 'ArraySetter',
            props: {
              itemSetter: {
                componentName: 'StringSetter',
                props: {
                  placeholder: '',
                },
              },
            },
          },
        },
      ],
      component: {
        isContainer: true,
        disableBehaviors: '*',
      },
      supports: false,
    },
  },
});

export default Slot;
